@font-face {
    font-family: Product-Sans-Bold-italic;
    src: url(https://unpkg.com/aks-fonts@1.0.0/Product-Sans/Product-Sans-Bold-italic.ttf);
}

@font-face {
    font-family: Product-Sans-Bold;
    src: url(https://unpkg.com/aks-fonts@1.0.0/Product-Sans/Product-Sans-Bold.ttf);
}

@font-face {
    font-family: Product-Sans-Regular;
    src: url(https://unpkg.com/aks-fonts@1.0.0/Product-Sans/Product-Sans-Regular.ttf);
}

@font-face {
    font-family: Product-Sans-italic;
    src: url(https://unpkg.com/aks-fonts@1.0.0/Product-Sans/Product-Sans-italic.ttf);
}

*,
::after,
::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    /* background: #c9dee0; */
    color: #666;
}

body {
    font-family: Product-Sans-Regular;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.cv {
    background: white;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    /* padding: 10px; */
    border-top: 8px solid black;
    position: relative;
    /* margin-top: 2rem; */
    margin-bottom: 2rem;
    overflow: hidden;
    border-radius: 5px;
}

.cv-row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    position: relative;
}

.cv-wrap {
    width: 50%;
    margin-right: 1rem;
    display: flex;
    flex-direction: column;
}

.cv-wrap:nth-child(2n) {
    width: 35%;
    margin-right: 0rem;
}

.cv-name {
    font-family: Product-Sans-Bold;
    font-size: 2rem;
    font-weight: 900;
    line-height: normal;
    margin-bottom: 0.5rem;
    color: black;
    width: fit-content;
}

.cv-subname {
    font-family: Product-Sans-Regular;
    font-size: 1.5rem;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 2rem;
    color: #555555;
    width: fit-content;
}

.cv-content {
    margin: 10px;
}

.cv-content .head-title {
    font-family: Product-Sans-Regular;
    font-size: 1rem;
    font-weight: normal;
    line-height: normal;
    margin-bottom: 0.5rem;
    color: #555555;
    width: fit-content;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.cv-content-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
}

.cv-content-item .title {
    font-family: Product-Sans-Bold;
    font-size: 1.3rem;
    font-weight: 900;
    line-height: normal;
    margin-bottom: 0.4rem;
    color: black;
    width: fit-content;
}

.cv-content-item .subtitle {
    font-family: Product-Sans-Regular;
    font-size: 1rem;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 0.2rem;
    color: #555555;
    width: fit-content;
}

.cv-content-item .time {
    font-family: Product-Sans-Regular;
    font-size: 0.9rem;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 0.4rem;
    color: #555555;
    width: fit-content;
}

.cv-content-item .exprecince {
    font-family: Product-Sans-Regular;
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.5;
    color: #131313;
    width: fit-content;
}

.avatar {
    width: 150px;
    height: 150px;
    border-radius: 9999px;
    overflow: hidden;
    position: relative;
    margin-bottom: 2rem;
    box-shadow: 0 5px 30px #00000029;
}

.avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;
}

.info {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.info .title {
    font-family: Product-Sans-Regular;
    font-size: 1.3rem;
    font-weight: 200;
    line-height: normal;
    margin-bottom: 0.5rem;
    color: #434343;
    width: fit-content;
}

.info p {
    margin-bottom: 0.3rem;
}

.info a {
    font-family: Product-Sans-Regular;
    font-size: 1.1rem;
    font-weight: 500;
    line-height: normal;
    color: #686868;
    width: fit-content;
    text-decoration: none;
}

.info a:hover {
    color: #004eff;
}

.cv-skills {
    margin-bottom: 0.5rem;
}

.cv-skills .head-title {
    font-family: Product-Sans-Regular;
    font-size: 1.3rem;
    font-weight: 200;
    line-height: normal;
    margin-bottom: 0.5rem;
    color: #040404;
    width: fit-content;
}

.cv-skills .title {
    font-family: Product-Sans-Regular;
    font-size: 1rem;
    font-weight: 200;
    line-height: normal;
    margin-bottom: 0.5rem;
    color: #504f4f;
    width: fit-content;
}

.cv-social {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
    position: relative;
    margin-top: 1rem;
}

.cv-social a {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1rem;
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

.cv-social a:hover {
    color: #004eff;
}

.cv-social-tag {
    width: 40px;
    height: 40px;
    overflow: hidden;
}